@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  margin: 0;
  background-color: rgba(255, 255, 255, 0.295);
}
div p {
  text-align: center;
  font-size: 26px;
  font-family: "Architects Daughter", cursive;
  color: black;
}
.ExpensesLoading {
  text-align: center;
  color: white;
}
.ExpensesError {
  text-align: center;
  color: red;
}

.new-expense__controls {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 3rem;
  gap: 3rem;
  margin-bottom: 2rem;
  text-align: left;
  color: rgb(3, 3, 3);
}

.new-expense__control label {
  font-weight: bold;
  margin-bottom: 1rem;
  display: block;
}

.new-expense__control input {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid rgba(15, 15, 14, 0.774);
  width: 20rem;
  max-width: 100%;
}

.new-expense__actions {
  text-align: right;
}
.new-expense__error{
  color: rgb(163, 20, 20);
}
.invalid input {
  border: 1px solid #b40e0e;
  background-color: #fddddd;
}
.invalid input:focus {
  border: 1px solid #f38200;
  background-color: #fbe8d2;
}
button:disabled,
button:disabled:hover,
button:disabled:active {
  background-color: #ccc;
  color: #777272;
  border-color: #ccc;
  cursor: not-allowed;
}
.new-expense {
  background-color: whitesmoke;
  padding: 1rem;
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.new-expense button {
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 1px solid rgb(7, 7, 7);
  background-color: whitesmoke;
  color: rgb(0, 0, 0);
  border-radius: 12px;
  margin-right: 1rem;
  box-shadow: aliceblue;
}

.new-expense button:hover,
.new-expense button:active {
  background-color: whitesmoke;
  border-color: black;
}

.new-expense button.alternative {
  color: #eaebe996;
  border-color: transparent;
  background-color: transparent;
}

.new-expense button.alternative:hover,
.new-expense button.alternative:active {
  background-color: #eaebe996;
}

.card {
  border-radius: 12px;
  box-shadow: 0 1px 8px rgb(190, 105, 34);
}

.expenses-filter {
  color: black;
  padding: 0 1rem;
}

.expenses-filter__control {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  margin: 1rem 0;
}

.expenses-filter label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.expenses-filter select {
  font: inherit;
  padding: 0.5rem 3rem;
  font-weight: bold;
  border-radius: 6px;
}
.expense-date {
  display: flex;
  flex-direction: column;
  width: 5.5rem;
  height: 5.5rem;
  border: 1px solid black;
  background-color: whitesmoke;
  color: rgb(3, 3, 3);
  border-radius: 12px;
  align-items: center;
  justify-content: center;
}

.expense-date__month {
  font-size: 0.75rem;
  font-weight: bold;
}

.expense-date__year {
  font-size: 0.75rem;
}

.expense-date__day {
  font-size: 1.5rem;
  font-weight: bold;
}

.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  margin: 1rem 0;
  background-color: whitesmoke;
}

.expense-item__description {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  flex: 1 1;
}

.expense-item h2 {
  color: #3a3a3a;
  font-size: 1rem;
  flex: 1 1;
  margin: 0 1rem;
  color: rgb(22, 21, 21);
}

.expense-item__price {
  font-size: 1rem;
  font-weight: bold;
  color: black;
  background-color: whitesmoke;
  border: 1px solid black;
  padding: 0.5rem;
  border-radius: 12px;
}
.expense-item__amount {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background-color: whitesmoke;
  border: 1px solid black;
  padding: 0.5rem;
  border-radius: 12px;
}
.expense-item__name {
  font-size: 1rem;
  font-weight: bold;
  color: black;
  padding: 0.2rem;
  text-align: center;
}
.actions {
  display: flex;
  flex-direction: column;
}
.expense-item button {
  font: inherit;
  font-weight: bold;
  font-size: 1.25rem;
  color: black;
  border: 1px solid black;
  width: 3rem;
  text-align: center;
  border-radius: 6px;
  background-color: transparent;
  cursor: pointer;
  margin-left: 1rem;
  margin: 0.25rem;
}

.expense-item button:hover,
.expense-item button:active {
  background-color: rgba(216, 216, 216, 0.404);
  opacity: 1;
  color: black;
}

@media only screen and (max-width: 640px) {
  .expense-item__description h2 {
    font-size: 0.8rem;
  }
  .expense-item__price {
    text-align: left;
    font-size: 0.75rem;
    padding: 0.5rem 1.5rem;
  }
}

@media only screen and (min-width: 768px) {
  .expense-item__description {
    grid-gap: 1rem;
    gap: 1rem;
  }
}

.expenses-list {
  list-style: none;
  padding: 0;
}

.expenses-list__fallback {
  color: rgb(10, 10, 10);
  text-align: center;
}
.expenses {
  padding: 1rem;
  background-color: rgb(255, 255, 255);
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
}

